*
    margin: 0px
    padding: 0px
    box-sizing: border-box
body
    margin-top: 0px
.grid-container
    display: grid
    grid-template-columns: 1fr 268px 780px 268px 1fr
    grid-template-rows: 75px 200px 200px 200px 200px auto
.navbar
    display: grid
    align-items: center
    grid-template-columns: 1fr 248px 740px 248px 1fr
    grid-template-rows: 75px
    grid-column: 1/-1
    background-color: #FA6400
    align-items: center
    #levelop
        grid-column: 2
        grid-row: 1
.new-post
    display: flex
    justify-content: center
    align-items: center
    width: 162px
    height: 42px
    background-color: #ffffff
    border-radius: 5px
    grid-column: 4
    grid-row: 1
    justify-self: end
.leftsidecontainer
    margin-top: 25%
    display: flex
    flex-wrap: wrap
    grid-column: 2
    grid-row: 2
.profile-pic
    margin: 0 auto
    margin-bottom: 7%
    width: 128px
    height: 128px
    background-image: url("./Images/Face dp2.jpg")
    border-radius: 100px
    background-color: #000000
.profile
    align-items: center
.karma
    display: inline
    margin-left: 10%
.username
    font-family: Poppins
    font-weight: 600
    font-size: 24px
    color: #000000
.handlename
    font-family: Poppins
    font-weight: 600
    font-size: 18px
    color: #000000
    p
        display: inline
.menu
    margin-top: 25%
    font-weight: 600
    svg
        margin-right: 2vh
.feed
    font-family: poppins
    font-size: 24px
    margin-bottom: 15%
.settings
    font-family: poppins
    font-size: 24px
    margin-bottom: 15%
.saved
    margin-left: 3%
    font-size: 24px
    font-family: poppins
    svg
        margin-right: 2.5vh
.right-container
    font-family: poppins
    display: grid
    grid-column: 4
.heading-tag
    font-family: Poppins
    font-weight: 600
    font-size: 24px
    letter-spacing: 0.3px
    color: #6b6a6a
    border-bottom: 1px solid #AFAFAF
    text-align: center
    margin-top: 40%
.tags
    font-family: Poppins
    font-style: normal
    font-weight: 600
    font-size: 18px
    line-height: 30px
    letter-spacing: 0.3px
    margin: 0 auto
    margin-top: 7%
    color: #6b6a6a
    p
        margin-top: 5%
.mid-container
    width: 41vw
    align-items: center
    grid-column: 3
    grid-row: 2/-1
.quick-search
    grid-column: 3/4
    display: flex
    width: 740px
    justify-content: space-evenly
    margin-top: 6%
.post-container
    margin-top: 5vh
.post
    display: flex
    width: 740px
    grid-column: 3/4
    height: 150px
    background: #FFFFFF
    box-shadow: 0px 0px 18px rgba(0, 0, 0, 0.1)
    border-radius: 6px
    margin-bottom: 2.5%
.postimg-container
    display: flex
    align-items: center
    justify-content: center
    margin-left: 1.5vw
    width: 112px
    height: 112px
.post-text
    margin-top: 4vh
    font-family: poppins
    color: black
    text-decoration: none
    margin-left: 2vw
    width: 700px
    height: 61px
    font-style: normal
    font-weight: bold
    font-size: 27px
#search, #filter
    display: flex
    align-items: center
    justify-content: center
    width: 36px
    height: 36px
    border-radius: 20px
    background: #FFFFFF
    box-shadow: 0px 0px 18px rgba(0, 0, 0, 0.1)
#latest, #design
    display: flex
    align-items: center
    justify-content: center
    width: 80px
    height: 36px
    background: #FFFFFF
    box-shadow: 0px 0px 18px rgba(0, 0, 0, 0.1)
    border-radius: 35px
    font-family: Poppins
    font-style: normal
    font-weight: 600
    font-size: 16px
#todays, #development
    display: flex
    align-items: center
    justify-content: center
    width: 131px
    height: 36px
    background: #FFFFFF
    box-shadow: 0px 0px 18px rgba(0, 0, 0, 0.1)
    border-radius: 35px
    font-family: Poppins
    font-style: normal
    font-weight: 600
    font-size: 16px
